cc.Class({
    extends: cc.Component,

    properties: {
        server_type: cc.Node,
        server_name: cc.Node
    },

    // LIFE-CYCLE CALLBACKS:

    initInfo (info) {
        // 初始化该道具相关信息

        // 图片
        var self = this;
        
        if(info['picUrl']){
            cc.loader.loadRes(info['picUrl'], cc.SpriteFrame, function (err, spriteFrame) {
                self.node.getComponent(cc.Sprite).spriteFrame = spriteFrame;
            });
        }

        console.log(self.node)
        // // 介绍
        self.node.children.server_type = info['type'];
        // // 介绍
        self.node.children.server_name = info['name'];



    },
});
